@use '../utils/variables' as *;

.about {
    background-color: $aboutBcgColor;
    padding-top: 125px;
    padding-bottom: 97px;
}

.section-title {
    font-family: "Titan One", serif;
    font-weight: 400;
    text-align: center;
    color: $textColor;
    font-size: 18px;

    @media screen and (min-width:$tablet) {
        font-size: 26px;
    }

    @media screen and (min-width:$desktop) {
        font-size: 30px;
    }
}

.section-subtitle {
    font-family: "Titan One", serif;
    font-weight: 400;
    margin-left: auto;
    margin-right: auto;
    margin-top: 15px;
    margin-bottom: 26px;
    text-align: center;
    text-transform: uppercase;
    color: $mainTextColor;

    @media screen and (min-width:$mobile) {
        font-size: 34px;
        width: 200px;
    }

    @media screen and (min-width:$tablet) {
        font-size: 48px;
        width: 429px;
    }

    @media screen and (min-width:$desktop) {
        font-size: 58px;
        width: 534px;
    }
}

.wrapper-content {
    background-image: url(../images/sectionbg1.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 0 340px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-position: 0 100%;

    @media screen and (min-width:$tablet) {
        flex-direction: row;
        gap: 20px;
    }
}

