.wrapper-content-text {
    font-family: "DM Sans", serif;
    font-weight: 500;
    width: 220px;
    line-height: 1.5;

    &-light {
        font-size: 14px;
        margin-top: 33px;
        color: $lightGrayColor;

        @media screen and (min-width:$desktop) {
            font-size: 14px;
        }
    }

    @media screen and (min-width:$mobile) {
        width: 220px;
        font-size: 12px;
    }

    @media screen and (min-width:$tablet) {
        width: 340px;
        font-size: 14px;
    }

    @media screen and (min-width:$desktop) {
        width: 463px;
        font-size: 16px;
    }
}

.about-btn {
    font-family: "DM Sans", serif;
    width: 176px;
    height: 44px;
    font-weight: 700;
    font-size: 16px;
    border-radius: 22px;
    color: $mainTextColor;
    background-color: $whiteColor;
    fill: $mainTextColor;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    display: block;
    margin: 17px auto 0 auto;

    &:hover,
    &:focus {
        background-color: $darkWhiteColor;
    }

    @media screen and (min-width:$mobile) {
        margin-top: 22px;
    }

    @media screen and (min-width:$tablet) {
        margin-top: 62px;
    }

    @media screen and (min-width:$desktop) {
        margin-top: 45px;
    }
}

.about-list {
    margin-top: 39px;

    @media screen and (min-width:$tablet) {
        display: flex;
    }
}

.about-item { 
    margin-bottom: 38px;
    &:nth-child(1)::before {
        content: '';
        display: block;
        background-size: cover;
        background-repeat: no-repeat;
        background-image: url(../images/icon1.png);
        width: 33px;
        height: 50px;
    }
    &:nth-child(2)::before {
        content: '';
        display: block;
        background-size: cover;
        background-repeat: no-repeat;
        background-image: url(../images/icon2.png);
        width: 50px;
        height: 50px;
    }
    &:nth-child(3)::before {
        content: '';
        display: block;
        background-size: cover;
        background-repeat: no-repeat;
        background-image: url(../images/icon3.png);
        width: 44px;
        height: 50px;
    }
}
.about-number {
    font-family: "Titan One", serif;
    font-weight: 400;
    font-size: 58px;
    color: $mainTextColor;
    margin-top: 16px;
    letter-spacing: 1.44px;

    @media screen and (min-width: $tablet) {
        margin-top: 36px;
    }
    @media screen and (min-width: $desktop) {
        margin-top: 40px;
    }
}

.about-text {
    font-family: "DM Sans", serif;
    font-weight: 700;
    font-size: 16px;

    @media screen and (min-width: $tablet) {
        margin-top: 13px;
        line-height: 1.7;
    }

    @media screen and (min-width: $desktop) {
        margin-top: 15px;
    }

}